import * as React from 'react';
import { useMemo } from 'react';
import { AlignRightCellStyles, ColumnProps, PaddedCellStyles, TableCellStyles } from './Table';
import Decimal from "decimal.js-light";
import { TableCell, TableFooter, TableRow } from '@material-ui/core';
import { cx } from 'emotion';

export interface TotalColumnProps<T> extends ColumnProps<T> {
  showTotal?: boolean;
  totalRender?: (value: number, index: number) => React.ReactNode;
}

interface TotalsFooterProps<T> {
  data: T[];
  paddedCells?: boolean;
  columns: TotalColumnProps<T>[];
  filteredItems?: T[];
}

// tslint:disable-next-line:function-name
export default function TotalsFooter<T extends object>({
  data,
  columns,
  paddedCells,
  filteredItems,
}: TotalsFooterProps<T>) {
  const isFiltered = !!filteredItems && filteredItems.length < data.length;
  const totalsItems = isFiltered ? filteredItems : data;
  const columnTotals = useMemo(
    () => columns.map((column) => {
      return {
        ...column,
        total: column.showTotal ?
          totalsItems?.reduce(
            (total: number, item: any) =>
              new Decimal((item[column.dataIndex] || 0) as number).plus(total).toNumber(),
            0,
          ) as number : undefined,
      };
    }),
    [columns, totalsItems],
  );

  return (
    <TableFooter>
      <TableRow>
        {
          columnTotals.map((columnTotal, index) =>
            <TableCell
              className={cx(
                TableCellStyles,
                {[PaddedCellStyles]: paddedCells},
                {[AlignRightCellStyles]: columnTotal.align === 'right'}
              )}
              key={index}>
            {
              index === 0 ? `Total: ${totalsItems?.length}`
              :
              columnTotal.total ?
                columnTotal.totalRender ?
                  columnTotal.totalRender(columnTotal.total, index)
                : columnTotal.total
              :
              ' '
            }
            </TableCell>
          )
        }
      </TableRow>
    </TableFooter>
  );
}
