
import { Table } from 'components/table/Table'
import React from 'react'
import Account from 'types/Account';
import styled from '@emotion/styled';
import { css } from 'emotion';
import { Fab } from '@material-ui/core';
import { MdAdd } from 'react-icons/md';
import { useHistory } from 'react-router';
import { AccountTypeLabels } from 'types/AccountType';
import LazyDialog from 'components/LazyDialog';
import Country, { CountryLabels } from 'types/Country';

const NewAccountDialog = React.lazy(() => import('components/organisms/NewAccountDialog'))

const Img = styled.img`
  width: 48px;
  vertical-align: bottom;
`;

const NewButtonStyle = css`
  font-size: 2em;
  position: fixed;
  right: 24px;
  bottom: 24px;
`;

const AccountsList = () => {
  const { push } = useHistory();
  const [isNewAccountModalOpen, setIsNewAccountModalOpen] = React.useState(false);

  const handleRowClick = (row: Account) => {
    push(`/accounts/${row.id}`);
  };

  const onNewButtonClick = () => {
    setIsNewAccountModalOpen(true);
  }

  const onNewAccountModalClose = () => {
    setIsNewAccountModalOpen(false);
  }

  const onNewAccountModalOK = () => {
    setIsNewAccountModalOpen(false);
    push(`/accounts/4`);
  }

  return (
    <>
      <Table<Account>
        paddedCells
        height={720}
        frozenHeader
        // localStorageStateKey='client'
        // data={data?.items}
        data={[
          {
            id: 1,
            avatar: 'marvel.png',
            name: 'Marvel Cinematic Universe',
            type: 1,
            jurisdiction: Country.AU,
          },
          {
            id: 2,
            avatar: 'amc.jpg',
            name: 'Breaking Bad',
            type: 1,
            jurisdiction: Country.AU,
          },
          {
            id: 3,
            avatar: 'black-noir.jpg',
            name: 'The Boys',
            type: 3,
            jurisdiction: Country.AU,
          }
        ]}
        columns={[
          {
            title: '',
            dataIndex: 'avatar',
            width: 100,
            render: (value, record) => <Img src={`/avatars/${value}`} alt={record.name} />
          },
          {
            title: 'Name',
            dataIndex: 'name',
          },
          {
            title: 'Type',
            dataIndex: 'type',
            render: (value, record) => AccountTypeLabels.get(value)
          },
          {
            title: 'Jurisdiction',
            dataIndex: 'jurisdiction',
            render: (value, record) => CountryLabels.get(value)
          },
        ]}
        // sort={sortState}
        // filters={filterState}
        // pagination={paginationState}
        onRowClick={handleRowClick}
        // onPaginationChange={setPaginationState}
        // onSortChange={setSortState}
        // onFilterChange={setFilterState}
      />
      <LazyDialog isOpen={isNewAccountModalOpen}>
        <NewAccountDialog
          isOpen={isNewAccountModalOpen}
          onClose={onNewAccountModalClose}
          onSubmit={onNewAccountModalOK}
        />
      </LazyDialog>
      <Fab aria-label="add" color="primary" className={NewButtonStyle} onClick={onNewButtonClick}>
        <MdAdd />
      </Fab>
    </>
  );
}

export default AccountsList;
